@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  width: 100vw;
  height: 100vh;
  margin: 0;
  background-color: aquamarine;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h2 {
  text-align: center;
}

#root {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.DropDown_button__3TkjH {
  background-color: #e74c3c;
  font-family: Lato, Arial;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: auto;
  top: 20vh;
  width: 300px;
  text-align: center;
  border: none;
  font-weight: 700;
  border-radius: 10px;
  overflow: hidden;
  transition: all 0.5s ease-in;
}

.DropDown_options__2cNgj {
  position: relative;
  top: 20vh;
  width: 300px;
  height: auto;
  cursor: pointer;
  border-radius: 10px;
  overflow: hidden;
}

/* UpArrow/DownArrow positioning */

.DropDown_mainArea__2u8A5 {
  display: flex;
  flex-direction: row;
}

.DropDown_arrow__1AjO5 {
  position: absolute;
  right: 10px;
}

/* Common style for options */

.DropDown_commonStyle__4FmDn {
  text-align: center;
  color: white;
  width: 100%;
  border-top: 2px solid rgb(192, 164, 164);
  margin: 0;
  padding: 10px 0px;
  background-color: #c0392b;
}

.DropDown_commonStyle__4FmDn:hover {
  background-color: white;
  color: #c0392b;
}

